import React from "react";
import "./Jumbotron.css";

function Jumbotron() {
  return (
    <div className="jumbotron text-center">
      <h1>Clicky Game!</h1>
      <h4>Click on an image to earn points, but don't click on any more than once!</h4>
    </div>
  );
}

export default Jumbotron;